body {
  background: #FAD61D;
  font-family: Cambria;
}


.center {
  text-align: center;
}

.upper {
  text-transform: capitalize;
  }

#pokeball {

  height: 30px;
  width: 30px;

}

#pokeball {
  -webkit-animation: pokeball infinite 1s linear;
          animation: pokeball infinite 1s linear;
}

@-webkit-keyframes pokeball {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes pokeball {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


